import React, { useCallback, useEffect } from 'react';
import styled from 'styled-components';
import { Container } from 'react-grid-system';
import { useGetIndicators } from '../../_hooks';
import { LoadingOutlined } from '@ant-design/icons';

const MainCont = styled.div`
  //background-color: ${props => props.theme.main.primaryColor};
  color: #008AFC;
  padding: 2rem 0;
  font-size: 14px;
  user-select: none;
  @media(min-width: 768px){
    padding: 1rem 0;
  }
`
const RatesCont = styled.ul`
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: #fff;
  @media(min-width: 768px){
    color: #919191;
    font-weight: normal;
    justify-content: flex-end;
    color: #008AFC;
  }
`
const RateItem = styled.li`
  //margin-left: .30rem;
  &::after{
    margin: 0 .5rem;
    content: "|"
  }
  @media(min-width: 768px){
    //margin-left: .5rem;
    &::after{
      margin: 0 1rem;
      content: "|"
    } 
  }
`
const RateItemNoAfter = styled(RateItem)`
  &::after{
    content: ""
  }
  @media(min-width: 768px){
    &::after{
      margin: 0;
      content: ""
    } 
  }
`

export default ()=> {
  const { loading, error, data } = useGetIndicators();
  
  if(loading) return(
    <MainCont>
      <Container>
        <RatesCont>
          <RateItem>
            UF <span><LoadingOutlined /></span>
          </RateItem>
          <RateItem>
            UTM <span><LoadingOutlined /></span>
          </RateItem>
          <RateItemNoAfter>
            Dlar <span><LoadingOutlined /></span>
          </RateItemNoAfter>                    
        </RatesCont>
      </Container>
    </MainCont>
  );

  if(error) return <span>error de conextin</span>

  return(
    <MainCont>
      <Container>
        <RatesCont>
          <RateItem>
            UF ${data.uf}
          </RateItem>
          <RateItem>
            UTM ${data.utm}
          </RateItem>
          <RateItemNoAfter>
            Dlar ${data.dollar}
          </RateItemNoAfter>                    
        </RatesCont>
      </Container>
    </MainCont>
  )
}